<template>
  <div id="main5" style="width: 500px;height:500px;" ref="myCharts"></div>
</template>

<script>
  // import echarts from 'echarts'
  export default {
    data () {
      return {
        option: {
          title: {
            text: '',
            left: 'center'
          },
          tooltip: {
            trigger: 'item',
            formatter: '{a} <br/>{b} : {c} ({d}%)'
          },
          legend: {
            left: 'center',
            top: 'bottom',
            data: ['', '', '', '', '', '', '']
          },
          toolbox: {
            show: true,
            feature: {
              mark: {show: true},
              dataView: {show: true, readOnly: false},
              restore: {show: true},
              saveAsImage: {show: true}
            }
          },
          series: [
            {
              name: '',
              type: 'pie',
              radius: [20, 140],
              center: ['50 %', '50%'],
              roseType: 'radius',
              itemStyle: {
                borderRadius: 5
              },
              data: [
                {value: 30, name: ''},
                {value: 28, name: ''},
                {value: 26, name: ''},
                {value: 24, name: ''},
                {value: 22, name: ''},
                {value: 20, name: ''},
                {value: 18, name: ''}
              ]
            }
          ]
        }
      }
    },
    mounted () {
      let myChart = this.$echarts.init(document.getElementById('main5'));
      myChart.setOption(this.option);
    },
    methods: {

    }
  }
</script>

