<template>
  <div class="">
    <div class="head"></div>
    <el-table :data="items">
      <el-table-column prop="_id" label="ID" width="230">
      </el-table-column>
      <el-table-column prop="name" label="" width="140">
      </el-table-column>

      <el-table-column prop="tel" label="" width="140">
      </el-table-column>

      <el-table-column prop="wechat" label="" width="140">
      </el-table-column>

      <el-table-column prop="img" label="">
        <template slot-scope="scope">
          <img :src="scope.row.avatar" alt="" style="height: 3rem">
        </template>
      </el-table-column>

      <el-table-column prop="username" label="" width="120">
      </el-table-column>
      <el-table-column prop="password" label="">
      </el-table-column>
      <el-table-column prop="type" label="">
      </el-table-column>

      <el-table-column
          fixed="right"
          label=""
          width="220">
        <template slot-scope="scope">

          <el-button type="primary" size="small" @click="$router.push(`/photographer/edit/${scope.row._id}`)"></el-button>
          <el-button type="warning" size="small" @click="remove(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        items: []
      }
    },

    methods: {
      async fetch() {
        const res = await this.$http.get('rest/photographer')
        this.items = res.data
      },

      async remove(row) {
        this.$confirm(`"${row.name}", ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
          const res = await this.$http.delete(`rest/photographer/${row._id}`)
          this.$message({
            type: 'success',
            message: '!'
          });
          this.fetch()
        })
      }
    },
    created() {
      this.fetch()

    },

  };
</script>

<style scoped>
  .head {
    border-left: 5px solid #09b4c5;
    height: 50px;
    line-height: 50px;
    padding-left: 20px;
    background: #f2f2f2;
    font-family: "microsoft yahei";
    font-weight: 600;
    color: #919191;
  }
</style>



